import { Injectable, inject } from '@angular/core';
import { Observable, forkJoin, map, catchError, of } from 'rxjs';
import {
  TelemetryComparisonService,
  AlignedRecord,
  TelemetryRecord,
  FieldComparison,
} from './telemetry-comparison.service';
import {
  EventsComparisonService,
  AlignedEventRecord,
  EventRecord,
} from './events-comparison.service';
import {
  TripsComparisonService,
  AlignedTripRecord,
  TripRecord,
} from './trips-comparison.service';
import { isDefined, isNullOrUndefined } from '../../../shared/utils';

export type TimelineRecordType =
  | 'telemetry'
  | 'event'
  | 'trip_start'
  | 'trip_end';

export type RecordUnion = TelemetryRecord | EventRecord | TripRecord;
export interface UnifiedTimelineRecord {
  type: TimelineRecordType;
  timestamp: string;
  index: number;
  timestampDiff?: number;
  testRecord: RecordUnion | null;
  prodRecord: RecordUnion | null;
  status: 'both-environments' | 'cypherView-only' | 'key-only';
  matchingFields?: number;
  totalFields?: number;
  matchPercentage?: number;
  data?: unknown; // Store the original aligned record for detailed view
}

export interface UnifiedTimelineSummary {
  totalRecords: number;
  telemetryCount: number;
  eventsCount: number;
  tripsCount: number;
  bothPresentCount: number;
  testOnlyCount: number;
  prodOnlyCount: number;
  overallMatchRate: number;
  qualityStatus: string;
  timeRange: {
    start: string;
    end: string;
    duration: string;
  };
}

export interface UnifiedTimelineResult {
  records: UnifiedTimelineRecord[];
  summary: UnifiedTimelineSummary;
  rawData: {
    telemetry: {
      alignedRecords: AlignedRecord[];
      testRecords: TelemetryRecord[];
      prodRecords: TelemetryRecord[];
    };
    events: {
      alignedRecords: AlignedEventRecord[];
      testRecords: EventRecord[];
      prodRecords: EventRecord[];
    };
    trips: {
      alignedRecords: AlignedTripRecord[];
      testRecords: TripRecord[];
      prodRecords: TripRecord[];
    };
  };
}

@Injectable({
  providedIn: 'root',
})
export class UnifiedTimelineService {
  private readonly telemetryService = inject(TelemetryComparisonService);
  private readonly eventsService = inject(EventsComparisonService);
  private readonly tripsService = inject(TripsComparisonService);

  /**
   * Get unified timeline comparing telemetry, events, and trips data
   * Replicates Python merge_json_reports.py functionality
   */
  getUnifiedTimeline(
    vehicleId: string,
    startDate: Date,
    endDate: Date,
    limit?: number,
    deviceName?: string,
  ): Observable<UnifiedTimelineResult> {
    // Fetch all data types in parallel (like Python does)
    const telemetryData$ = this.telemetryService.getTelemetryComparison(
      vehicleId,
      startDate,
      endDate,
      limit,
      deviceName,
    );
    const eventsData$ = this.eventsService.getEventsComparison(
      vehicleId,
      startDate,
      endDate,
      limit,
      deviceName,
    );
    const tripsData$ = this.tripsService.getTripsComparison(
      vehicleId,
      startDate,
      endDate,
      limit,
      deviceName,
    );

    return forkJoin({
      telemetry: telemetryData$,
      events: eventsData$,
      trips: tripsData$,
    }).pipe(
      map(({ telemetry, events, trips }) => {
        // Merge all records into unified timeline (replicates merge_records() function)
        const unifiedRecords = this.mergeRecordsChronologically(
          telemetry,
          events,
          trips,
        );

        // Calculate unified summary (replicates create_unified_report() function)
        const summary = this.calculateUnifiedSummary(
          unifiedRecords,
          startDate,
          endDate,
        );

        return {
          records: unifiedRecords,
          summary,
          rawData: { telemetry, events, trips },
        };
      }),
      catchError((error) => {
        console.error('‚ùå Unified timeline error:', error);
        return of({
          records: [],
          summary: this.getEmptyUnifiedSummary(startDate, endDate),
          rawData: {
            telemetry: { alignedRecords: [], testRecords: [], prodRecords: [] },
            events: { alignedRecords: [], testRecords: [], prodRecords: [] },
            trips: { alignedRecords: [], testRecords: [], prodRecords: [] },
          },
        });
      }),
    );
  }

  /**
   * Merge records from all data types and sort chronologically
   * Replicates Python merge_records() functionality
   */
  private mergeRecordsChronologically(
    telemetry: { alignedRecords: AlignedRecord[] },
    events: { alignedRecords: AlignedEventRecord[] },
    trips: { alignedRecords: AlignedTripRecord[] },
  ): UnifiedTimelineRecord[] {
    const unifiedRecords: UnifiedTimelineRecord[] = [];

    // Convert telemetry records
    telemetry.alignedRecords.forEach((record: AlignedRecord, index: number) => {
      unifiedRecords.push(
        this.convertToUnifiedRecord('telemetry', record, index),
      );
    });

    // Convert event records
    events.alignedRecords.forEach(
      (record: AlignedEventRecord, index: number) => {
        unifiedRecords.push(
          this.convertToUnifiedRecord('event', record, index),
        );
      },
    );

    // Convert trip records - create two records per trip (start and end)
    trips.alignedRecords.forEach((record: AlignedTripRecord, index: number) => {
      // Create trip start record
      unifiedRecords.push(
        this.convertToUnifiedRecord('trip_start', record, index),
      );
      // Create trip end record
      unifiedRecords.push(
        this.convertToUnifiedRecord('trip_end', record, index),
      );
    });

    // Sort chronologically by timestamp (replicates Python sorting)
    return unifiedRecords.sort((a, b) => {
      try {
        const timeA = new Date(a.timestamp).getTime();
        const timeB = new Date(b.timestamp).getTime();

        // Check for invalid dates
        if (isNaN(timeA) || isNaN(timeB)) {
          console.warn('‚ö†Ô∏è Invalid timestamp found:', {
            recordA: a.type,
            timestampA: a.timestamp,
            recordB: b.type,
            timestampB: b.timestamp,
          });
          return 0;
        }

        return timeA - timeB;
      } catch (error) {
        console.error('‚ùå Error sorting records:', error, {
          recordA: a,
          recordB: b,
        });
        return 0;
      }
    });
  }

  /**
   * Convert individual aligned records to unified format
   * Replicates get_record_timestamp() and record conversion logic
   */
  private convertToUnifiedRecord(
    type: TimelineRecordType,
    alignedRecord: AlignedRecord | AlignedEventRecord | AlignedTripRecord,
    index: number,
  ): UnifiedTimelineRecord {
    const testRecord = alignedRecord.testRecord;
    const prodRecord = alignedRecord.prodRecord;

    // Extract timestamp (prioritize test record, fallback to prod)
    const timestamp =
      this.extractTimestamp(testRecord, type) ??
      this.extractTimestamp(prodRecord, type) ??
      new Date().toISOString();

    // Determine record status
    let status: 'both-environments' | 'cypherView-only' | 'key-only';
    if (testRecord && prodRecord) {
      status = 'both-environments';
    } else if (testRecord && !prodRecord) {
      status = 'cypherView-only';
    } else {
      status = 'key-only';
    }

    // Calculate field matching for this record type
    const { matchingFields, totalFields, matchPercentage } =
      this.calculateRecordMatching(type, alignedRecord);

    return {
      type,
      timestamp,
      index,
      timestampDiff: alignedRecord.timestampDiff,
      testRecord,
      prodRecord,
      status,
      matchingFields,
      totalFields,
      matchPercentage,
      data: alignedRecord, // Store original for detailed comparison
    };
  }

  /**
   * Extract timestamp from any record type
   * Replicates Python get_record_timestamp() logic
   */
  private extractTimestamp(
    record: RecordUnion | null,
    recordType?: TimelineRecordType,
  ): string | null {
    if (isNullOrUndefined(record)) return null;

    const timestampKeys = this.getTimestampKeys(recordType);

    for (const key of timestampKeys) {
      const ts = record[key] as string | number | undefined;

      if (isDefined(ts)) {
        try {
          const normalizedTs = typeof ts === 'string' ? ts : ts.toString();
          const testDate = new Date(normalizedTs);
          if (!isNaN(testDate.getTime())) {
            return normalizedTs;
          }
        } catch {
          console.warn('‚ö†Ô∏è Invalid timestamp format encountered:', ts);
        }
      }
    }

    return null;
  }

  /**
   * Get the priority-ordered list of keys to check for a timestamp.
   */
  private getTimestampKeys(recordType?: TimelineRecordType): string[] {
    switch (recordType) {
      case 'event':
        return ['eventDate', 'eventTime', 'timestamp', 'received', 'date'];
      case 'trip_start':
        return ['startTime', 'timestamp', 'date'];
      case 'trip_end':
        return ['endTime', 'timestamp', 'date'];
      default:
        return ['timestamp', 'received', 'date'];
    }
  }

  /**
   * Calculate field matching percentage for a record
   * Uses appropriate comparison service based on record type
   */
  private calculateRecordMatching(
    type: TimelineRecordType,
    alignedRecord: AlignedRecord | AlignedEventRecord | AlignedTripRecord,
  ): { matchingFields: number; totalFields: number; matchPercentage: number } {
    let comparisons: FieldComparison[] = [];

    switch (type) {
      case 'telemetry':
        comparisons = this.telemetryService.compareFields(
          alignedRecord.testRecord,
          alignedRecord.prodRecord,
        );
        break;
      case 'event':
        comparisons = this.eventsService.compareFields(
          alignedRecord.testRecord,
          alignedRecord.prodRecord,
        );
        break;
      case 'trip_start':
      case 'trip_end':
        comparisons = this.tripsService.compareFields(
          alignedRecord.testRecord,
          alignedRecord.prodRecord,
        );
        break;
    }

    const totalFields = comparisons.length;
    const matchingFields = comparisons.filter(
      (c) => c.status === 'identical',
    ).length;
    const matchPercentage =
      totalFields > 0 ? (matchingFields / totalFields) * 100 : 0;

    return { matchingFields, totalFields, matchPercentage };
  }

  /**
   * Calculate unified summary statistics
   * Replicates Python create_unified_report() summary logic
   */
  private calculateUnifiedSummary(
    records: UnifiedTimelineRecord[],
    startDate: Date,
    endDate: Date,
  ): UnifiedTimelineSummary {
    const totalRecords = records.length;
    const telemetryCount = records.filter((r) => r.type === 'telemetry').length;
    const eventsCount = records.filter((r) => r.type === 'event').length;
    const tripsCount = records.filter(
      (r) => r.type === 'trip_start' || r.type === 'trip_end',
    ).length;

    const bothPresentCount = records.filter(
      (r) => r.status === 'both-environments',
    ).length;
    const testOnlyCount = records.filter(
      (r) => r.status === 'cypherView-only',
    ).length;
    const prodOnlyCount = records.filter((r) => r.status === 'key-only').length;

    // Calculate overall match rate across all record types
    const totalMatches = records.reduce(
      (sum, r) => sum + (r.matchingFields ?? 0),
      0,
    );
    const totalFields = records.reduce(
      (sum, r) => sum + (r.totalFields ?? 0),
      0,
    );
    const overallMatchRate =
      totalFields > 0 ? (totalMatches / totalFields) * 100 : 0;

    // Determine quality status
    let qualityStatus = 'üî¥ Poor';
    if (overallMatchRate >= 95) qualityStatus = 'üü¢ Excellent';
    else if (overallMatchRate >= 85) qualityStatus = 'üü° Good';
    else if (overallMatchRate >= 70) qualityStatus = 'üü† Fair';

    const duration = endDate.getTime() - startDate.getTime();
    const durationHours = Math.round((duration / (1000 * 60 * 60)) * 10) / 10;

    return {
      totalRecords,
      telemetryCount,
      eventsCount,
      tripsCount,
      bothPresentCount,
      testOnlyCount,
      prodOnlyCount,
      overallMatchRate,
      qualityStatus,
      timeRange: {
        start: startDate.toISOString(),
        end: endDate.toISOString(),
        duration: `${durationHours}h`,
      },
    };
  }

  /**
   * Get empty summary for error cases
   */
  private getEmptyUnifiedSummary(
    startDate: Date,
    endDate: Date,
  ): UnifiedTimelineSummary {
    const duration = endDate.getTime() - startDate.getTime();
    const durationHours = Math.round((duration / (1000 * 60 * 60)) * 10) / 10;

    return {
      totalRecords: 0,
      telemetryCount: 0,
      eventsCount: 0,
      tripsCount: 0,
      bothPresentCount: 0,
      testOnlyCount: 0,
      prodOnlyCount: 0,
      overallMatchRate: 0,
      qualityStatus: '‚ùå No Data',
      timeRange: {
        start: startDate.toISOString(),
        end: endDate.toISOString(),
        duration: `${durationHours}h`,
      },
    };
  }

  /**
   * Export unified timeline as JSON (replicates Python JSON export)
   */
  exportAsJson(result: UnifiedTimelineResult): string {
    return JSON.stringify(
      {
        summary: result.summary,
        records: result.records,
        metadata: {
          generated: new Date().toISOString(),
          tool: 'CypherView Unified Timeline',
          version: '1.0',
        },
      },
      null,
      2,
    );
  }

  /**
   * Export unified timeline as detailed text report (replicates Python text export)
   */
  exportAsTextReport(result: UnifiedTimelineResult): string {
    const { summary, records } = result;

    let report = `UNIFIED TIMELINE COMPARISON REPORT\n`;
    report += `Generated: ${new Date().toISOString()}\n`;
    report += `\n`;
    report += `SUMMARY:\n`;
    report += `--------\n`;
    report += `Total Records: ${summary.totalRecords}\n`;
    report += `- Telemetry: ${summary.telemetryCount}\n`;
    report += `- Events: ${summary.eventsCount}\n`;
    report += `- Trips: ${summary.tripsCount}\n`;
    report += `\n`;
    report += `Status Distribution:\n`;
    report += `- Both Environments: ${summary.bothPresentCount}\n`;
    report += `- CypherView Only: ${summary.testOnlyCount}\n`;
    report += `- Key Only: ${summary.prodOnlyCount}\n`;
    report += `\n`;
    report += `Overall Match Rate: ${summary.overallMatchRate.toFixed(1)}% (${summary.qualityStatus})\n`;
    report += `Time Range: ${summary.timeRange.start} - ${summary.timeRange.end} (${summary.timeRange.duration})\n`;
    report += `\n`;
    report += `CHRONOLOGICAL TIMELINE:\n`;
    report += `=======================\n`;

    records.forEach((record, index) => {
      report += `\n[${index + 1}] ${record.timestamp} - ${record.type.toUpperCase()}\n`;
      report += `Status: ${record.status}\n`;
      if (record.matchPercentage !== undefined) {
        report += `Field Match: ${record.matchingFields}/${record.totalFields} (${record.matchPercentage.toFixed(1)}%)\n`;
      }
      if (record.timestampDiff !== undefined) {
        report += `Time Diff: ¬±${record.timestampDiff.toFixed(1)}s\n`;
      }
      report += `---\n`;
    });

    return report;
  }
}
