import { Injectable, inject } from '@angular/core';
import { Observable, forkJoin, map, catchError, of } from 'rxjs';
import { TripsService } from './trips.service';
import { DualApiService } from './dual-api.service';
import { isDefined, isNullOrUndefined } from '../../../shared/utils';
import { IdNameType, TripResponse } from '../../../api';

export interface TripRecord {
  timestamp?: string;
  startTime?: string;
  endTime?: string;
  tripId?: string;
  distance?: number;
  duration?: number;
  averageSpeed?: number;
  maxSpeed?: number;
  fuelUsed?: number;
  startLocation?: {
    lat?: number;
    lon?: number;
    address?: string;
    [key: string]: unknown;
  };
  endLocation?: {
    lat?: number;
    lon?: number;
    address?: string;
    [key: string]: unknown;
  };
  zones?: {
    id?: string;
    name?: string;
    [key: string]: unknown;
  }[];
  tripData?: Record<string, unknown>;
  origin?: {
    id?: string;
    name?: string;
  };
  start?: {
    date?: string;
    lat?: number;
    lon?: number;
    address?: string;
    odometer?: number;
    hours?: number;
    [key: string]: unknown;
  };
  maxes?: {
    speed?: number;
    [key: string]: unknown;
  };
  [key: string]: unknown;
}

export interface AlignedTripRecord {
  testRecord: TripRecord | null;
  prodRecord: TripRecord | null;
  index: number;
  timestampDiff?: number;
}

export interface TripComparisonSummary {
  totalAligned: number;
  bothPresent: number;
  testOnly: number;
  prodOnly: number;
  matchRate: number;
  qualityStatus: string;
}

export interface TripFieldComparison {
  field: string;
  testValue: string;
  prodValue: string;
  status: 'identical' | 'different' | 'missing_test' | 'missing_prod';
  displayTest: string;
  displayProd: string;
}

@Injectable({
  providedIn: 'root',
})
export class TripsComparisonService {
  private readonly tripsService = inject(TripsService);
  private readonly dualApiService = inject(DualApiService);

  getTripsComparison(
    vehicleId: string,
    startDate: Date,
    endDate: Date,
    limit?: number,
    deviceName?: string,
  ): Observable<{
    alignedRecords: AlignedTripRecord[];
    summary: TripComparisonSummary;
    testRecords: TripRecord[];
    prodRecords: TripRecord[];
  }> {
    // Fetch trips from both APIs in parallel
    const cypherViewTrips$ = this.tripsService
      .getTrips(
        vehicleId,
        startDate.toISOString(),
        endDate.toISOString(),
        limit,
      )
      .pipe(
        map((response) => response.items ?? []),
        catchError((error) => {
          console.error('‚ùå CypherView trips error:', error);
          return of([]);
        }),
      );

    const keyTrips$ = this.dualApiService.getKeyApiTrips(
      vehicleId,
      startDate.toISOString(),
      endDate.toISOString(),
      limit,
      deviceName,
    );

    return forkJoin({
      testData: cypherViewTrips$.pipe(
        map((items: TripResponse[]) => this.mapToTripRecords(items)),
      ),
      prodData: keyTrips$.pipe(
        map((items) => this.mapToTripRecords(items as TripResponse[])),
      ),
    }).pipe(
      map(({ testData, prodData }) => {
        const alignedRecords = this.alignRecordsByTimestamp(testData, prodData);
        const summary = this.calculateSummary(alignedRecords);

        return {
          alignedRecords,
          summary,
          testRecords: testData,
          prodRecords: prodData,
        };
      }),
      catchError((error) => {
        console.error('Error fetching trips comparison:', error);
        return of({
          alignedRecords: [],
          summary: this.getEmptySummary(),
          testRecords: [],
          prodRecords: [],
        });
      }),
    );
  }

  private mapToTripRecords(items: TripResponse[]): TripRecord[] {
    return items.map(
      (item: TripResponse) =>
        ({
          ...item,
          timestamp:
            item['startTime'] ??
            item['timestamp'] ??
            item['received'] ??
            item['date'],
          startTime:
            item['startTime'] ?? item.start?.['date'] ?? item.dateStart,
          endTime: item['endTime'] ?? item.end?.['date'] ?? item.dateEnd,
        }) as TripRecord,
    );
  }

  setKeyApiCredentials(username: string, password: string): void {
    this.dualApiService.setCredentials(
      { username: '', password: '' }, // CypherView uses existing auth
      { username, password }, // Key API credentials from user
    );
  }

  private alignRecordsByTimestamp(
    testRecords: TripRecord[],
    prodRecords: TripRecord[],
    toleranceSeconds = 30, // Trips may have larger time tolerance
  ): AlignedTripRecord[] {
    const aligned: AlignedTripRecord[] = [];
    const usedProdIndices = new Set<number>();

    // Parse timestamp helper
    const parseTimestamp = (record: TripRecord | null): Date | null => {
      if (!record) return null;
      const tsStr =
        record.timestamp ??
        record.startTime ??
        record['start']?.date ??
        record['received'];
      if (isNullOrUndefined(tsStr)) return null;

      try {
        return new Date((tsStr as string).replace('Z', '+00:00'));
      } catch {
        return null;
      }
    };

    // Sort records by timestamp
    const sortedTestRecords = [...testRecords].sort((a, b) => {
      const aTime = parseTimestamp(a);
      const bTime = parseTimestamp(b);
      if (!aTime || !bTime) return 0;
      return aTime.getTime() - bTime.getTime();
    });

    const sortedProdRecords = [...prodRecords]
      .map((record, index) => ({ record, originalIndex: index }))
      .sort((a, b) => {
        const aTime = parseTimestamp(a.record);
        const bTime = parseTimestamp(b.record);
        if (!aTime || !bTime) return 0;
        return aTime.getTime() - bTime.getTime();
      });

    // Match test records with production records
    sortedTestRecords.forEach((testRecord, index) => {
      const testTime = parseTimestamp(testRecord);
      let bestMatch: TripRecord | null = null;
      let bestDiff = Infinity;
      let bestIdx: number | null = null;

      if (testTime) {
        sortedProdRecords.forEach(({ record: prodRecord, originalIndex }) => {
          if (usedProdIndices.has(originalIndex)) return;

          const prodTime = parseTimestamp(prodRecord);
          if (prodTime) {
            const diff = Math.abs(
              (testTime.getTime() - prodTime.getTime()) / 1000,
            );
            if (diff <= toleranceSeconds && diff < bestDiff) {
              bestMatch = prodRecord;
              bestDiff = diff;
              bestIdx = originalIndex;
            }
          }
        });
      }

      if (isDefined(bestMatch) && bestIdx !== null) {
        usedProdIndices.add(bestIdx);
        aligned.push({
          testRecord,
          prodRecord: bestMatch,
          index,
          timestampDiff: bestDiff,
        });
      } else {
        aligned.push({
          testRecord,
          prodRecord: null,
          index,
          timestampDiff: undefined,
        });
      }
    });

    // Add unmatched production records
    sortedProdRecords.forEach(({ record: prodRecord, originalIndex }) => {
      if (!usedProdIndices.has(originalIndex)) {
        aligned.push({
          testRecord: null,
          prodRecord,
          index: aligned.length,
          timestampDiff: undefined,
        });
      }
    });

    return aligned;
  }

  private calculateSummary(
    alignedRecords: AlignedTripRecord[],
  ): TripComparisonSummary {
    const bothPresent = alignedRecords.filter(
      (r) => isDefined(r.testRecord) && isDefined(r.prodRecord),
    ).length;
    const testOnly = alignedRecords.filter(
      (r) => isDefined(r.testRecord) && !isDefined(r.prodRecord),
    ).length;
    const prodOnly = alignedRecords.filter(
      (r) => !isDefined(r.testRecord) && isDefined(r.prodRecord),
    ).length;
    const totalAligned = alignedRecords.length;

    const matchRate = totalAligned > 0 ? (bothPresent / totalAligned) * 100 : 0;

    let qualityStatus = 'üî¥ Poor';
    if (matchRate >= 95) qualityStatus = 'üü¢ Excellent';
    else if (matchRate >= 85) qualityStatus = 'üü° Good';
    else if (matchRate >= 70) qualityStatus = 'üü† Fair';

    return {
      totalAligned,
      bothPresent,
      testOnly,
      prodOnly,
      matchRate,
      qualityStatus,
    };
  }

  private getEmptySummary(): TripComparisonSummary {
    return {
      totalAligned: 0,
      bothPresent: 0,
      testOnly: 0,
      prodOnly: 0,
      matchRate: 0,
      qualityStatus: '‚ùå No Data',
    };
  }

  private formatTimestampForDisplay(timestamp: string): string {
    if (!timestamp) return '';
    try {
      const dt = new Date(timestamp);
      return dt.toLocaleString('en-GB', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
      });
    } catch {
      return timestamp;
    }
  }

  private isTimestamp(value: string): boolean {
    if (!value) return false;
    try {
      const dt = new Date(value);
      return (
        !isNaN(dt.getTime()) &&
        (value.includes('T') ||
          value.includes('Z') ||
          !!RegExp(/\d{4}-\d{2}-\d{2}/).exec(value))
      );
    } catch {
      return false;
    }
  }

  extractAllFields(record: TripRecord | null): Record<string, string> {
    if (!record) return {};

    const fields: Record<string, string> = {};

    this._extractDateTimeFields(record, fields);
    this._extractTripInfoFields(record, fields);
    this._extractMeasurementFields(record, fields);
    this._extractAssetInfo(record, fields);
    this._extractLocationFields(record, fields);
    this._extractZoneFields(record, fields);

    return fields;
  }

  private _extractDateTimeFields(
    record: TripRecord,
    fields: Record<string, string>,
  ) {
    const startTime = record.startTime ?? record.timestamp ?? '';
    fields['dateStart'] = this.isTimestamp(startTime)
      ? this.formatTimestampForDisplay(startTime)
      : startTime;

    const endTime = record.endTime ?? '';
    fields['dateEnd'] = this.isTimestamp(endTime)
      ? this.formatTimestampForDisplay(endTime)
      : endTime;

    const creationDate = record['creationDate'] ?? '';
    const creationDateStr = String(creationDate as string);
    fields['creationDate'] = this.isTimestamp(creationDateStr)
      ? this.formatTimestampForDisplay(creationDateStr)
      : creationDateStr;
  }

  private _extractTripInfoFields(
    record: TripRecord,
    fields: Record<string, string>,
  ) {
    const idVal = record.tripId ?? record['id'] ?? '';
    fields['id'] = typeof idVal === 'string' ? idVal : String(idVal as string);
  }

  private _formatValue(value: unknown, unit: string, toFixed: number): string {
    if (value == null) return '';
    try {
      return `${Number(value).toFixed(toFixed)} ${unit}`;
    } catch {
      return String((value as string) ?? '');
    }
  }

  private _extractMeasurementFields(
    record: TripRecord,
    fields: Record<string, string>,
  ) {
    const distance = record.distance ?? record['distance'] ?? null;
    fields['distance'] = this._formatValue(distance, 'km', 2);

    const duration = record.duration ?? record['drivingTime'] ?? null;
    fields['drivingTime'] = this._formatValue(duration, 'min', 2);

    const maxSpeed = record.maxSpeed ?? record['maxes']?.speed;
    fields['maxSpeed'] = this._formatValue(maxSpeed, 'km/h', 2);

    if (fields['distance'] && fields['drivingTime']) {
      try {
        const distanceKm = Number(fields['distance'].replace(' km', ''));
        const durationHours =
          Number(fields['drivingTime'].replace(' min', '')) / 60;
        if (durationHours > 0) {
          fields['averageSpeed'] =
            `${(distanceKm / durationHours).toFixed(2)} km/h`;
        } else {
          fields['averageSpeed'] = '';
        }
      } catch {
        fields['averageSpeed'] = '';
      }
    } else {
      fields['averageSpeed'] = '';
    }

    const fuelUsed = record.fuelUsed ?? record['fuel'] ?? null;
    fields['fuelUsed'] = this._formatValue(fuelUsed, 'L', 2);

    const idleTime = record['idleTime'];
    if (idleTime != null) {
      try {
        fields['idleTime'] = `${(Number(idleTime) / 60).toFixed(2)} min`;
      } catch {
        fields['idleTime'] = String((idleTime as string) ?? '');
      }
    } else {
      fields['idleTime'] = '';
    }
  }

  private _extractAssetInfo(
    record: TripRecord,
    fields: Record<string, string>,
  ) {
    const asset = record['asset'] as IdNameType;
    if (isDefined(asset)) {
      fields['asset.name'] = asset.name ?? '';
    }
  }

  private _extractLocationFields(
    record: TripRecord,
    fields: Record<string, string>,
  ) {
    const start = record.startLocation || record['start'];
    if (isDefined(start)) {
      fields['startAddress'] = start.address ?? '';
      fields['start.latitude'] = this._formatValue(start.lat, '', 6);
      fields['start.longitude'] = this._formatValue(start.lon, '', 6);
      fields['start.odometer'] = this._formatValue(start['odometer'], 'km', 2);
      fields['start.hours'] = this._formatValue(start['hours'], 'h', 2);
    }

    const end =
      record.endLocation || (record['end'] as TripRecord['endLocation']);
    if (isDefined(end)) {
      fields['endAddress'] = end.address ?? '';
      fields['end.latitude'] = this._formatValue(end.lat, '', 6);
      fields['end.longitude'] = this._formatValue(end.lon, '', 6);
      fields['end.odometer'] = this._formatValue(end['odometer'], 'km', 2);
      fields['end.hours'] = this._formatValue(end['hours'], 'h', 2);
    }
  }

  private _extractZoneFields(
    record: TripRecord,
    fields: Record<string, string>,
  ) {
    const zones = record.zones ?? [];
    if (Array.isArray(zones)) {
      const zoneNames = zones.map((z) => z.name).filter(Boolean);
      const zoneIds = zones.map((z) => z.id).filter(Boolean);

      fields['zones.names'] = zoneNames.join(', ');
      fields['zones.ids'] = zoneIds.join(', ');
      fields['zones.count'] = String(zones.length);
    } else {
      fields['zones.names'] = '';
      fields['zones.ids'] = '';
      fields['zones.count'] = '0';
    }
  }

  compareFields(
    testRecord: TripRecord | null,
    prodRecord: TripRecord | null,
  ): TripFieldComparison[] {
    const testFields = this.extractAllFields(testRecord);
    const prodFields = this.extractAllFields(prodRecord);

    const allFields = new Set([
      ...Object.keys(testFields),
      ...Object.keys(prodFields),
    ]);

    // Sort fields with priority matching Python implementation
    const priorityFields = [
      'dateStart',
      'dateEnd',
      'id',
      'distance',
      'drivingTime',
      'averageSpeed',
      'maxSpeed',
      'fuelUsed',
      'idleTime',
    ];
    const sortedFields = [
      ...priorityFields.filter((f) => allFields.has(f)),
      ...Array.from(allFields)
        .filter((f) => !priorityFields.includes(f))
        .sort((a, b) => a.localeCompare(b)),
    ];

    return sortedFields.map((field) => {
      const testValue = testFields[field] || '';
      const prodValue = prodFields[field] || '';

      let status: 'identical' | 'different' | 'missing_test' | 'missing_prod';
      let displayTest = testValue;
      let displayProd = prodValue;

      // Normalize values for comparison
      const normalizeValue = (val: string) => {
        if (!val) return '';
        try {
          return String(parseFloat(val));
        } catch {
          return val.trim();
        }
      };

      const testNormalized = normalizeValue(testValue);
      const prodNormalized = normalizeValue(prodValue);

      if (!testValue && prodValue) {
        status = 'missing_test';
        displayTest = '‚ùå MISSING';
      } else if (testValue && !prodValue) {
        status = 'missing_prod';
        displayProd = '‚ùå MISSING';
      } else if (testNormalized !== prodNormalized && testValue && prodValue) {
        status = 'different';
        displayTest = `üîπ ${testValue}`;
        displayProd = `üî∏ ${prodValue}`;
      } else if (testNormalized === prodNormalized && testValue) {
        status = 'identical';
        displayTest = `‚úÖ ${testValue}`;
        displayProd = `‚úÖ ${prodValue}`;
      } else {
        status = 'identical';
      }

      return {
        field,
        testValue,
        prodValue,
        status,
        displayTest,
        displayProd,
      };
    });
  }
}
