import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { HistoryService } from '../../../api/api/history.service';
import { TripListResponse } from '../../../api/model/tripListResponse';

@Injectable({
  providedIn: 'root',
})
export class TripsService {
  private readonly historyService = inject(HistoryService);

  /**
   * Get trips for a specific asset within a date range
   * Wraps the HistoryService.getTripHistory method
   */
  getTrips(
    assetId: string,
    startDate: string,
    endDate: string,
    limit?: number,
  ): Observable<TripListResponse> {
    return this.historyService.getTripHistory(
      assetId,
      startDate,
      endDate,
      undefined,
      limit,
    );
  }
}
