import { Injectable, inject } from '@angular/core';
import { Observable, forkJoin, map, catchError, of } from 'rxjs';
import { EventsService } from './events.service';
import { DualApiService } from './dual-api.service';
import { isDefined, isNullOrUndefined } from '../../../shared/utils';
import { EventResponse, IdNameType } from '../../../api';

export interface EventRecord {
  timestamp?: string;
  received?: string;
  eventTime?: string;
  eventType?: string;
  eventId?: string;
  priority?: number;
  location?: {
    lat?: number;
    lon?: number;
    accuracy?: number;
    gc?: Record<string, unknown>;
    [key: string]: unknown;
  };
  zones?: {
    id?: string;
    name?: string;
    [key: string]: unknown;
  }[];
  eventData?: Record<string, unknown>;
  origin?: {
    id?: string;
    name?: string;
  };
  [key: string]: unknown;
}

export interface AlignedEventRecord {
  testRecord: EventRecord | null;
  prodRecord: EventRecord | null;
  index: number;
  timestampDiff?: number;
}

export interface EventComparisonSummary {
  totalAligned: number;
  bothPresent: number;
  testOnly: number;
  prodOnly: number;
  matchRate: number;
  qualityStatus: string;
}

export interface EventFieldComparison {
  field: string;
  testValue: string;
  prodValue: string;
  status: 'identical' | 'different' | 'missing_test' | 'missing_prod';
  displayTest: string;
  displayProd: string;
}

@Injectable({
  providedIn: 'root',
})
export class EventsComparisonService {
  private readonly eventsService = inject(EventsService);
  private readonly dualApiService = inject(DualApiService);

  getEventsComparison(
    vehicleId: string,
    startDate: Date,
    endDate: Date,
    limit?: number,
    deviceName?: string,
  ): Observable<{
    alignedRecords: AlignedEventRecord[];
    summary: EventComparisonSummary;
    testRecords: EventRecord[];
    prodRecords: EventRecord[];
  }> {
    // Fetch events from both APIs in parallel
    const cypherViewEvents$ = this.eventsService
      .getEvents(
        vehicleId,
        startDate.toISOString(),
        endDate.toISOString(),
        limit,
      )
      .pipe(
        map((response) => response.items ?? []),
        catchError((error) => {
          console.error('‚ùå CypherView events error:', error);
          return of([]);
        }),
      );

    const keyEvents$ = this.dualApiService.getKeyApiEvents(
      vehicleId,
      startDate.toISOString(),
      endDate.toISOString(),
      limit,
      deviceName,
    );

    return forkJoin({
      testData: cypherViewEvents$.pipe(
        map((items: EventResponse[]) => this.mapToEventRecords(items)),
      ),
      prodData: keyEvents$.pipe(
        map((items) => this.mapToEventRecords(items as EventResponse[])),
      ),
    }).pipe(
      map(({ testData, prodData }) => {
        const alignedRecords = this.alignRecordsByTimestamp(testData, prodData);
        const summary = this.calculateSummary(alignedRecords);

        return {
          alignedRecords,
          summary,
          testRecords: testData,
          prodRecords: prodData,
        };
      }),
      catchError((error) => {
        console.error('Error fetching events comparison:', error);
        return of({
          alignedRecords: [],
          summary: this.getEmptySummary(),
          testRecords: [],
          prodRecords: [],
        });
      }),
    );
  }

  private mapToEventRecords(items: EventResponse[]): EventRecord[] {
    return items.map(
      (item: EventResponse) =>
        ({
          ...item,
          timestamp:
            item.eventDate ??
            item['eventTime'] ??
            item['timestamp'] ??
            item['received'] ??
            item['date'],
        }) as EventRecord,
    );
  }

  setKeyApiCredentials(username: string, password: string): void {
    this.dualApiService.setCredentials(
      { username: '', password: '' }, // CypherView uses existing auth
      { username, password }, // Key API credentials from user
    );
  }

  private alignRecordsByTimestamp(
    testRecords: EventRecord[],
    prodRecords: EventRecord[],
    toleranceSeconds = 2,
  ): AlignedEventRecord[] {
    const aligned: AlignedEventRecord[] = [];
    const usedProdIndices = new Set<number>();

    // Parse timestamp helper
    const parseTimestamp = (record: EventRecord | null): Date | null => {
      if (!record) return null;
      const tsStr =
        record.timestamp ??
        record['eventDate'] ??
        record.eventTime ??
        record.received;
      if (isNullOrUndefined(tsStr)) return null;

      try {
        return new Date(String(tsStr as string).replace('Z', '+00:00'));
      } catch {
        return null;
      }
    };

    // Sort records by timestamp
    const sortedTestRecords = [...testRecords].sort((a, b) => {
      const aTime = parseTimestamp(a);
      const bTime = parseTimestamp(b);
      if (!aTime || !bTime) return 0;
      return aTime.getTime() - bTime.getTime();
    });

    const sortedProdRecords = [...prodRecords]
      .map((record, index) => ({ record, originalIndex: index }))
      .sort((a, b) => {
        const aTime = parseTimestamp(a.record);
        const bTime = parseTimestamp(b.record);
        if (!aTime || !bTime) return 0;
        return aTime.getTime() - bTime.getTime();
      });

    // Match test records with production records
    sortedTestRecords.forEach((testRecord, index) => {
      const testTime = parseTimestamp(testRecord);
      let bestMatch: EventRecord | null = null;
      let bestDiff = Infinity;
      let bestIdx: number | null = null;

      if (testTime) {
        sortedProdRecords.forEach(({ record: prodRecord, originalIndex }) => {
          if (usedProdIndices.has(originalIndex)) return;

          const prodTime = parseTimestamp(prodRecord);
          if (prodTime) {
            const diff = Math.abs(
              (testTime.getTime() - prodTime.getTime()) / 1000,
            );
            if (diff <= toleranceSeconds && diff < bestDiff) {
              bestMatch = prodRecord;
              bestDiff = diff;
              bestIdx = originalIndex;
            }
          }
        });
      }

      if (isDefined(bestMatch) && bestIdx !== null) {
        usedProdIndices.add(bestIdx);
        aligned.push({
          testRecord,
          prodRecord: bestMatch,
          index,
          timestampDiff: bestDiff,
        });
      } else {
        aligned.push({
          testRecord,
          prodRecord: null,
          index,
          timestampDiff: undefined,
        });
      }
    });

    // Add unmatched production records
    sortedProdRecords.forEach(({ record: prodRecord, originalIndex }) => {
      if (!usedProdIndices.has(originalIndex)) {
        aligned.push({
          testRecord: null,
          prodRecord,
          index: aligned.length,
          timestampDiff: undefined,
        });
      }
    });

    return aligned;
  }

  private calculateSummary(
    alignedRecords: AlignedEventRecord[],
  ): EventComparisonSummary {
    const bothPresent = alignedRecords.filter(
      (r) => isDefined(r.testRecord) && isDefined(r.prodRecord),
    ).length;
    const testOnly = alignedRecords.filter(
      (r) => isDefined(r.testRecord) && !isDefined(r.prodRecord),
    ).length;
    const prodOnly = alignedRecords.filter(
      (r) => !isDefined(r.testRecord) && isDefined(r.prodRecord),
    ).length;
    const totalAligned = alignedRecords.length;

    const matchRate = totalAligned > 0 ? (bothPresent / totalAligned) * 100 : 0;

    let qualityStatus = 'üî¥ Poor';
    if (matchRate >= 95) qualityStatus = 'üü¢ Excellent';
    else if (matchRate >= 85) qualityStatus = 'üü° Good';
    else if (matchRate >= 70) qualityStatus = 'üü† Fair';

    return {
      totalAligned,
      bothPresent,
      testOnly,
      prodOnly,
      matchRate,
      qualityStatus,
    };
  }

  private getEmptySummary(): EventComparisonSummary {
    return {
      totalAligned: 0,
      bothPresent: 0,
      testOnly: 0,
      prodOnly: 0,
      matchRate: 0,
      qualityStatus: '‚ùå No Data',
    };
  }

  private formatTimestampForDisplay(timestamp: string): string {
    if (!timestamp) return '';
    try {
      const dt = new Date(timestamp);
      return dt.toLocaleString('en-GB', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
      });
    } catch {
      return timestamp;
    }
  }

  private isTimestamp(value: string): boolean {
    if (!value) return false;
    try {
      const dt = new Date(value);
      return (
        !isNaN(dt.getTime()) &&
        (value.includes('T') ||
          value.includes('Z') ||
          !!RegExp(/\d{4}-\d{2}-\d{2}/).exec(value))
      );
    } catch {
      return false;
    }
  }

  private setDateField(
    fields: Record<string, string>,
    key: string,
    value: unknown,
  ) {
    let str: string;
    if (value == null) str = '';
    else if (typeof value === 'object') str = JSON.stringify(value);
    else if (
      typeof value === 'string' ||
      typeof value === 'number' ||
      typeof value === 'boolean'
    )
      str = String(value);
    else str = JSON.stringify(value);
    fields[key] = this.isTimestamp(str)
      ? this.formatTimestampForDisplay(str)
      : str;
  }

  private setIdNameField(
    fields: Record<string, string>,
    key: string,
    value: IdNameType | undefined,
  ) {
    fields[key] = isDefined(value)
      ? (value.name ?? value.id ?? JSON.stringify(value))
      : '';
  }

  private setCountField(
    fields: Record<string, string>,
    key: string,
    value: unknown,
  ) {
    const arr = value ?? [];
    fields[key] = Array.isArray(arr) ? String(arr.length) : '0';
  }

  private extractLocationFields(
    fields: Record<string, string>,
    location: Record<string, unknown> | undefined,
  ) {
    if (!isDefined(location) || typeof location !== 'object') return;

    const safe = (k: string) => {
      const v = location?.[k];
      if (v == null) return '';
      if (typeof v === 'object') return JSON.stringify(v);
      if (
        typeof v === 'string' ||
        typeof v === 'number' ||
        typeof v === 'boolean'
      )
        return String(v);
      return JSON.stringify(v);
    };

    if (location['lat'] != null) {
      try {
        fields['location.latitude'] = Number(location['lat']).toFixed(6);
      } catch {
        fields['location.latitude'] = String(safe('lat'));
      }
    }

    if (location['lon'] != null) {
      try {
        fields['location.longitude'] = Number(location['lon']).toFixed(6);
      } catch {
        fields['location.longitude'] = String(safe('lon'));
      }
    }

    ['speed', 'heading', 'altitude', 'accuracy', 'age', 'address'].forEach(
      (k) => {
        if (location[k] != null) fields[`location.${k}`] = String(safe(k));
      },
    );

    const gc = location['gc'] ?? {};
    if (isDefined(gc) && typeof gc === 'object') {
      const gcObj = gc as Record<string, unknown>;
      fields['location.gc.ct'] = String((gcObj['ct'] as string) ?? '');
      fields['location.gc.pr'] = String((gcObj['pr'] as string) ?? '');
      fields['location.gc.tw'] = String((gcObj['tw'] as string) ?? '');
    }
  }

  private extractTelemetryFields(
    fields: Record<string, string>,
    telemetry: Record<string, unknown> | undefined,
  ) {
    if (!isDefined(telemetry) || typeof telemetry !== 'object') return;

    const telemetryData = telemetry['telemetry'] ?? {};
    if (isDefined(telemetryData) && typeof telemetryData === 'object') {
      Object.entries(telemetryData).forEach(([key, value]) => {
        if (value == null) return;
        const stringValue =
          typeof value === 'object'
            ? JSON.stringify(value)
            : String(value as string);
        fields[`telemetry.${key}`] =
          key === 'tco_timestamp' && this.isTimestamp(stringValue)
            ? this.formatTimestampForDisplay(stringValue)
            : stringValue;
      });
    }

    const zones = telemetry['zones'] ?? [];
    if (Array.isArray(zones)) {
      const zoneNames = zones.map((z) => z.name).filter(Boolean);
      const zoneIds = zones.map((z) => z.id).filter(Boolean);
      fields['zones.names'] = zoneNames.join(', ');
      fields['zones.ids'] = zoneIds.join(', ');
      fields['zones.count'] = String(zones.length);
    } else {
      fields['zones.names'] = '';
      fields['zones.ids'] = '';
      fields['zones.count'] = '0';
    }

    if (telemetry['active'] != null) {
      fields['active'] = String((telemetry['active'] as string) ?? '');
    }

    const timestamp = telemetry['date'] ?? telemetry['received'] ?? '';
    if (isDefined(timestamp)) {
      this.setDateField(fields, 'timestamp', timestamp);
    }
  }

  extractAllFields(record: EventRecord | null): Record<string, string> {
    if (!record) return {};

    const fields: Record<string, string> = {};

    // Basic event fields (matching Python extract_event_fields)
    fields['id'] = String((record['id'] as string) ?? record.eventId ?? '');
    fields['type'] = String(
      (record['type'] as string) ?? record.eventType ?? '',
    );
    fields['eventClass'] = String((record['eventClass'] as string) ?? '');
    fields['eventType'] = String((record['eventType'] as string) ?? '');

    // Dates
    const eventTime =
      record['eventDate'] ?? record.eventTime ?? record.timestamp ?? '';
    this.setDateField(fields, 'eventDate', eventTime);
    this.setDateField(fields, 'creationDate', record['creationDate']);
    this.setDateField(fields, 'modifiedDate', record['modifiedDate']);

    // Owner/origin
    this.setIdNameField(
      fields,
      'owner',
      record['owner'] as IdNameType | undefined,
    );
    this.setIdNameField(
      fields,
      'origin',
      record['origin'] as IdNameType | undefined,
    );

    // Counts
    this.setCountField(fields, 'linked.count', record['linked']);
    this.setCountField(fields, 'alerts.count', record['alerts']);
    this.setCountField(fields, 'notify.count', record['notify']);

    const details = (record['details'] ?? {}) as Record<string, unknown>;
    const telemetry = (details['telemetry'] ?? {}) as Record<string, unknown>;
    const location = (telemetry['location'] ??
      record['location'] ??
      {}) as Record<string, unknown>;

    this.extractLocationFields(fields, location);
    this.extractTelemetryFields(fields, telemetry);

    // Asset
    const asset: IdNameType =
      (details['asset'] as IdNameType) ?? (record['asset'] as IdNameType);
    if (isDefined(asset)) {
      fields['asset.name'] = asset.name ?? '';
      fields['asset.id'] = asset.id ?? '';
    }

    // Priority
    fields['priority'] = String(record.priority ?? '');

    return fields;
  }

  compareFields(
    testRecord: EventRecord | null,
    prodRecord: EventRecord | null,
  ): EventFieldComparison[] {
    const testFields = this.extractAllFields(testRecord);
    const prodFields = this.extractAllFields(prodRecord);

    const allFields = new Set([
      ...Object.keys(testFields),
      ...Object.keys(prodFields),
    ]);

    // Sort fields with priority matching Python implementation
    const priorityFields = [
      'id',
      'type',
      'eventClass',
      'eventType',
      'eventDate',
      'creationDate',
      'owner',
      'origin',
      'linked.count',
      'alerts.count',
      'notify.count',
    ];
    const sortedFields = [
      ...priorityFields.filter((f) => allFields.has(f)),
      ...Array.from(allFields)
        .filter((f) => !priorityFields.includes(f))
        .sort((a, b) => a.localeCompare(b)),
    ];

    return sortedFields.map((field) => {
      const testValue = testFields[field] || '';
      const prodValue = prodFields[field] || '';

      let status: 'identical' | 'different' | 'missing_test' | 'missing_prod';
      let displayTest = testValue;
      let displayProd = prodValue;

      // Normalize values for comparison
      const normalizeValue = (val: string) => {
        if (!val) return '';
        try {
          return String(parseFloat(val));
        } catch {
          return val.trim();
        }
      };

      const testNormalized = normalizeValue(testValue);
      const prodNormalized = normalizeValue(prodValue);

      if (!testValue && prodValue) {
        status = 'missing_test';
        displayTest = '‚ùå MISSING';
      } else if (testValue && !prodValue) {
        status = 'missing_prod';
        displayProd = '‚ùå MISSING';
      } else if (testNormalized !== prodNormalized && testValue && prodValue) {
        status = 'different';
        displayTest = `üîπ ${testValue}`;
        displayProd = `üî∏ ${prodValue}`;
      } else if (testNormalized === prodNormalized && testValue) {
        status = 'identical';
        displayTest = `‚úÖ ${testValue}`;
        displayProd = `‚úÖ ${prodValue}`;
      } else {
        status = 'identical';
      }

      return {
        field,
        testValue,
        prodValue,
        status,
        displayTest,
        displayProd,
      };
    });
  }
}
