import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { HistoryService } from '../../../api/api/history.service';
import { EventListResponse } from '../../../api/model/eventListResponse';

@Injectable({
  providedIn: 'root',
})
export class EventsService {
  private readonly historyService = inject(HistoryService);

  /**
   * Get events for a specific asset within a date range
   * Wraps the HistoryService.getEventHistory method
   */
  getEvents(
    assetId: string,
    startDate: string,
    endDate: string,
    limit?: number,
  ): Observable<EventListResponse> {
    return this.historyService.getEventHistory(
      assetId,
      startDate,
      endDate,
      limit,
    );
  }
}
