import { Injectable, inject } from '@angular/core';
import { Observable, forkJoin, map, catchError, of } from 'rxjs';
import { HistoryService } from '../../../api/api/history.service';
import { DataService } from '../../../api/api/data.service';
import { DualApiService } from './dual-api.service';
import { isDefined, isNullOrUndefined } from '../../../shared/utils';
import { TelemetryResponse } from '../../../api';

export interface TelemetryRecord {
  timestamp?: string;
  received?: string;
  active?: boolean;
  location?: {
    lat?: number;
    lon?: number;
    accuracy?: number;
    gc?: Record<string, unknown>;
    [key: string]: unknown;
  };
  zones?: {
    id?: string;
    name?: string;
    [key: string]: unknown;
  }[];
  telemetry?: Record<string, unknown>;
  origin?: {
    id?: string;
    name?: string;
  };
  [key: string]: unknown;
}

export interface AlignedRecord {
  testRecord: TelemetryRecord | null;
  prodRecord: TelemetryRecord | null;
  index: number;
  timestampDiff?: number;
}

export interface ComparisonSummary {
  totalAligned: number;
  bothPresent: number;
  testOnly: number;
  prodOnly: number;
  matchRate: number;
  qualityStatus: string;
}

export interface FieldComparison {
  field: string;
  testValue: string;
  prodValue: string;
  status: 'identical' | 'different' | 'missing_test' | 'missing_prod';
  displayTest: string;
  displayProd: string;
}

@Injectable({
  providedIn: 'root',
})
export class TelemetryComparisonService {
  private readonly historyService = inject(HistoryService);
  private readonly dataService = inject(DataService);
  private readonly dualApiService = inject(DualApiService);

  getTelemetryComparison(
    vehicleId: string,
    startDate: Date,
    endDate: Date,
    limit?: number,
    deviceName?: string,
  ): Observable<{
    alignedRecords: AlignedRecord[];
    summary: ComparisonSummary;
    testRecords: TelemetryRecord[];
    prodRecords: TelemetryRecord[];
  }> {
    // Fetch data from both APIs in parallel
    // Use HistoryService for CypherView (authenticated through existing app auth)
    const cypherViewData$ = this.historyService
      .getTelemetryHistory(
        vehicleId,
        startDate.toISOString(),
        endDate.toISOString(),
        limit,
      )
      .pipe(
        map((response) => response.items ?? []),
        catchError((error) => {
          console.error('‚ùå CypherView telemetry error:', error);
          return of([]);
        }),
      );

    const keyData$ = this.dualApiService.getKeyApiTelemetry(
      vehicleId,
      startDate.toISOString(),
      endDate.toISOString(),
      limit,
      deviceName, // Pass device name for vehicle mapping
    );

    return forkJoin({
      testData: cypherViewData$.pipe(
        map((items) => this.mapToTelemetryRecords(items)),
      ),
      prodData: keyData$.pipe(
        map((items) =>
          this.mapToTelemetryRecords(items as TelemetryResponse[]),
        ),
      ),
    }).pipe(
      map(({ testData, prodData }) => {
        const alignedRecords = this.alignRecordsByTimestamp(testData, prodData);
        const summary = this.calculateSummary(alignedRecords);

        return {
          alignedRecords,
          summary,
          testRecords: testData,
          prodRecords: prodData,
        };
      }),
      catchError((error) => {
        console.error('Error fetching telemetry comparison:', error);
        return of({
          alignedRecords: [],
          summary: this.getEmptySummary(),
          testRecords: [],
          prodRecords: [],
        });
      }),
    );
  }

  private mapToTelemetryRecords(items: TelemetryResponse[]): TelemetryRecord[] {
    return items.map(
      (item: TelemetryResponse) =>
        ({
          ...item,
          timestamp: item.date ?? item['timestamp'] ?? item.received,
        }) as TelemetryRecord,
    );
  }

  setKeyApiCredentials(username: string, password: string): void {
    this.dualApiService.setCredentials(
      { username: '', password: '' }, // CypherView uses existing auth
      { username, password }, // Key API credentials from user
    );
  }

  // For now, we'll simulate comparison by fetching the same data twice
  // TODO: Replace with actual test/prod API calls when Key API is available
  private simulateEnvironmentDifferences(
    records: TelemetryRecord[],
    environment: 'test' | 'prod',
  ): TelemetryRecord[] {
    if (environment === 'test') {
      return records; // Return original CypherView data as "test"
    }

    // Simulate "production" data with slight differences
    return records.map((record) => {
      const modifiedRecord = { ...record };

      // Add some simulated differences for demo purposes
      if (modifiedRecord.telemetry) {
        const telemetry = { ...modifiedRecord.telemetry };

        // Simulate small speed differences
        Object.keys(telemetry).forEach((key) => {
          if (
            key.toLowerCase().includes('speed') &&
            typeof telemetry[key] === 'object'
          ) {
            const speedData = telemetry[key] as { value?: number };
            if (speedData.value !== undefined) {
              speedData.value = speedData.value + Math.floor(Math.random() * 3);
            }
          }
        });

        modifiedRecord.telemetry = telemetry;
      }

      // Occasionally remove zones to simulate differences
      if (Math.random() > 0.8) {
        modifiedRecord.zones = [];
      }

      return modifiedRecord;
    });
  }

  private generateMockData(
    environment: 'test' | 'prod',
    vehicleId: string,
    startDate: Date,
    endDate: Date,
    limit?: number,
  ): TelemetryRecord[] {
    const records: TelemetryRecord[] = [];
    const duration = endDate.getTime() - startDate.getTime();
    const recordCount = Math.min(limit ?? 20, 20);

    for (let i = 0; i < recordCount; i++) {
      const timestamp = new Date(
        startDate.getTime() + (duration * i) / recordCount,
      );
      const record: TelemetryRecord = {
        timestamp: timestamp.toISOString(),
        received: timestamp.toISOString(),
        active: true,
        location: {
          lat: -26.2041 + (Math.random() - 0.5) * 0.01,
          lon: 28.0473 + (Math.random() - 0.5) * 0.01,
          accuracy: environment === 'test' ? 0 : Math.floor(Math.random() * 50),
          gc: {
            country: 'South Africa',
            city: 'Johannesburg',
          },
        },
        zones: [{ id: '123', name: 'Test Zone' }],
        telemetry: {
          speed: Math.floor(Math.random() * 120),
          ignition: true,
          fuel_level: 80 + Math.floor(Math.random() * 20),
          battery_voltage: 12.5 + Math.random() * 2,
        },
        origin: {
          id: vehicleId,
          name: `Vehicle ${vehicleId}`,
        },
      };

      // Add some differences between environments
      if (environment === 'prod' && record.telemetry) {
        record.telemetry['speed'] =
          (Number(record.telemetry['speed']) || 0) +
          Math.floor(Math.random() * 5);
        if (Math.random() > 0.8) {
          delete record.zones; // Sometimes missing in prod
        }
      }

      records.push(record);
    }

    return records;
  }

  private alignRecordsByTimestamp(
    testRecords: TelemetryRecord[],
    prodRecords: TelemetryRecord[],
    toleranceSeconds = 2,
  ): AlignedRecord[] {
    const aligned: AlignedRecord[] = [];
    const usedProdIndices = new Set<number>();

    // Parse timestamp helper
    const parseTimestamp = (record: TelemetryRecord | null): Date | null => {
      if (!record) return null;
      const tsStr = record.timestamp ?? record.received;
      if (isNullOrUndefined(tsStr)) return null;

      try {
        return new Date(tsStr.replace('Z', '+00:00'));
      } catch {
        return null;
      }
    };

    // Sort records by timestamp
    const sortedTestRecords = [...testRecords].sort((a, b) => {
      const aTime = parseTimestamp(a);
      const bTime = parseTimestamp(b);
      if (!aTime || !bTime) return 0;
      return aTime.getTime() - bTime.getTime();
    });

    const sortedProdRecords = [...prodRecords]
      .map((record, index) => ({ record, originalIndex: index }))
      .sort((a, b) => {
        const aTime = parseTimestamp(a.record);
        const bTime = parseTimestamp(b.record);
        if (!aTime || !bTime) return 0;
        return aTime.getTime() - bTime.getTime();
      });

    // Match test records with production records
    sortedTestRecords.forEach((testRecord, index) => {
      const testTime = parseTimestamp(testRecord);
      let bestMatch: TelemetryRecord | null = null;
      let bestDiff = Infinity;
      let bestIdx: number | null = null;

      if (testTime) {
        sortedProdRecords.forEach(({ record: prodRecord, originalIndex }) => {
          if (usedProdIndices.has(originalIndex)) return;

          const prodTime = parseTimestamp(prodRecord);
          if (prodTime) {
            const diff = Math.abs(
              (testTime.getTime() - prodTime.getTime()) / 1000,
            );
            if (diff <= toleranceSeconds && diff < bestDiff) {
              bestMatch = prodRecord;
              bestDiff = diff;
              bestIdx = originalIndex;
            }
          }
        });
      }

      if (isDefined(bestMatch) && bestIdx !== null) {
        usedProdIndices.add(bestIdx);
        aligned.push({
          testRecord,
          prodRecord: bestMatch,
          index,
          timestampDiff: bestDiff,
        });
      } else {
        aligned.push({
          testRecord,
          prodRecord: null,
          index,
          timestampDiff: undefined,
        });
      }
    });

    // Add unmatched production records
    sortedProdRecords.forEach(({ record: prodRecord, originalIndex }) => {
      if (!usedProdIndices.has(originalIndex)) {
        aligned.push({
          testRecord: null,
          prodRecord,
          index: aligned.length,
          timestampDiff: undefined,
        });
      }
    });

    return aligned;
  }

  private calculateSummary(alignedRecords: AlignedRecord[]): ComparisonSummary {
    const bothPresent = alignedRecords.filter(
      (r) => r.testRecord && r.prodRecord,
    ).length;
    const testOnly = alignedRecords.filter(
      (r) => isDefined(r.testRecord) && !r.prodRecord,
    ).length;
    const prodOnly = alignedRecords.filter(
      (r) => isNullOrUndefined(r.testRecord) && isDefined(r.prodRecord),
    ).length;
    const totalAligned = alignedRecords.length;

    const matchRate = totalAligned > 0 ? (bothPresent / totalAligned) * 100 : 0;

    let qualityStatus = 'üî¥ Poor';
    if (matchRate >= 95) qualityStatus = 'üü¢ Excellent';
    else if (matchRate >= 85) qualityStatus = 'üü° Good';
    else if (matchRate >= 70) qualityStatus = 'üü† Fair';

    return {
      totalAligned,
      bothPresent,
      testOnly,
      prodOnly,
      matchRate,
      qualityStatus,
    };
  }

  private getEmptySummary(): ComparisonSummary {
    return {
      totalAligned: 0,
      bothPresent: 0,
      testOnly: 0,
      prodOnly: 0,
      matchRate: 0,
      qualityStatus: '‚ùå No Data',
    };
  }

  private formatTimestampForDisplay(timestamp: string): string {
    if (!timestamp) return '';
    try {
      const dt = new Date(timestamp);
      return dt.toLocaleString('en-GB', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
      });
    } catch {
      return timestamp;
    }
  }

  extractAllFields(record: TelemetryRecord | null): Record<string, string> {
    if (!record) return {};

    const fields: Record<string, string> = {};

    // Basic fields (timestamp first, then active) - convert timestamp to local time
    fields['timestamp'] = this.formatTimestampForDisplay(
      record.timestamp ?? record.received ?? '',
    );
    fields['active'] = String(record.active ?? '');

    // Zones fields
    const zones = record.zones ?? [];
    if (Array.isArray(zones)) {
      const zoneNames = zones.map((z) => z.name).filter(Boolean);
      const zoneIds = zones.map((z) => z.id).filter(Boolean);

      fields['zones.names'] = zoneNames.join(', ');
      fields['zones.ids'] = zoneIds.join(', ');
      fields['zones.count'] = String(zones.length);
    }

    // Location fields
    const location = record.location ?? {};
    Object.entries(location).forEach(([key, value]) => {
      if (key === 'gc' && typeof value === 'object') {
        Object.entries(value as Record<string, unknown>).forEach(
          ([gcKey, gcValue]) => {
            fields[`location.gc.${gcKey}`] = String((gcValue as string) ?? '');
          },
        );
      } else if (
        ['lat', 'latitude', 'lon', 'longitude'].includes(key) &&
        value != null
      ) {
        try {
          fields[`location.${key}`] = Number(value).toFixed(6);
        } catch {
          fields[`location.${key}`] = String((value as string) ?? '');
        }
      } else {
        fields[`location.${key}`] = String((value as string) ?? '');
      }
    });

    // Telemetry fields
    const telemetry = record.telemetry ?? {};
    Object.entries(telemetry).forEach(([key, value]) => {
      // Transform CypherView system fields to standard field names
      let fieldName = key;
      if (key.startsWith('cvc_')) {
        fieldName = key.substring(4); // Remove cvc_ prefix
      } else if (key.startsWith('cve_')) {
        fieldName = key.substring(4); // Remove cve_ prefix
      } else if (key.startsWith('cv_')) {
        fieldName = key.substring(3); // Remove cv_ prefix
      }

      fields[`telemetry.${fieldName}`] = String((value as string) ?? '');
    });

    // Check for telemetry fields at root level if no telemetry object
    if (!record.telemetry) {
      const commonTelemetryFields = [
        'ignition',
        'priority',
        'eventId',
        'moving',
        'fuel_level',
        'engine_temp',
        'battery_voltage',
        'satellites',
        'hdop',
        'pdop',
        'gsm_signal',
        'total_mileage',
        'vehicle_speed',
        'accelerator_pedal_position',
        'driver_id',
        'fuel_consumed',
        'output_01',
        'output_02',
        'digital_01',
        'digital_02',
        'speed',
        'heading',
        'direction',
      ];

      commonTelemetryFields.forEach((field) => {
        if (field in record) {
          fields[field] = String((record[field] as string) ?? '');
        }
      });
    }

    return fields;
  }

  compareFields(
    testRecord: TelemetryRecord | null,
    prodRecord: TelemetryRecord | null,
  ): FieldComparison[] {
    const testFields = this.extractAllFields(testRecord);
    const prodFields = this.extractAllFields(prodRecord);

    const allFields = new Set([
      ...Object.keys(testFields),
      ...Object.keys(prodFields),
    ]);

    // Sort fields with priority: timestamp, active, zones, then alphabetically
    const priorityFields = [
      'timestamp',
      'active',
      'zones.count',
      'zones.names',
      'zones.ids',
    ];
    const sortedFields = [
      ...priorityFields.filter((f) => allFields.has(f)),
      ...Array.from(allFields)
        .filter((f) => !priorityFields.includes(f))
        .sort((a, b) => a.localeCompare(b)),
    ];

    return sortedFields.map((field) => {
      const testValue = testFields[field] || '';
      const prodValue = prodFields[field] || '';

      let status: 'identical' | 'different' | 'missing_test' | 'missing_prod';
      let displayTest = testValue;
      let displayProd = prodValue;

      // Normalize values for comparison
      const normalizeValue = (val: string) => {
        if (!val) return '';
        try {
          return String(parseFloat(val));
        } catch {
          return val.trim();
        }
      };

      const testNormalized = normalizeValue(testValue);
      const prodNormalized = normalizeValue(prodValue);

      if (!testValue && prodValue) {
        status = 'missing_test';
        displayTest = '‚ùå MISSING';
      } else if (testValue && !prodValue) {
        status = 'missing_prod';
        displayProd = '‚ùå MISSING';
      } else if (testNormalized !== prodNormalized && testValue && prodValue) {
        status = 'different';
        displayTest = `üîπ ${testValue}`;
        displayProd = `üî∏ ${prodValue}`;
      } else if (testNormalized === prodNormalized && testValue) {
        status = 'identical';
        displayTest = `‚úÖ ${testValue}`;
        displayProd = `‚úÖ ${prodValue}`;
      } else {
        status = 'identical';
      }

      return {
        field,
        testValue,
        prodValue,
        status,
        displayTest,
        displayProd,
      };
    });
  }
}
