import { Component, OnInit, inject, signal, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { SelectModule } from 'primeng/select';
import { AutoCompleteModule } from 'primeng/autocomplete';
import { DatePickerModule } from 'primeng/datepicker';
import { MessageModule } from 'primeng/message';
import { CardModule } from 'primeng/card';
import { ButtonModule } from 'primeng/button';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

import {
  ClientsService,
  AssetsService,
  ClientListResponse,
  AssetListResponse,
  AssetListItem,
} from '../../../../api';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { UtilityService } from '../../../../core/services/utility.service';
import { FilterMetadata } from 'primeng/api';
import {
  TelemetryComparisonService,
  AlignedRecord,
  ComparisonSummary,
  FieldComparison,
} from '../../services/telemetry-comparison.service';
import { EventsComparisonService } from '../../services/events-comparison.service';
import { TripsComparisonService } from '../../services/trips-comparison.service';
import {
  UnifiedTimelineService,
  UnifiedTimelineRecord,
  UnifiedTimelineSummary,
  UnifiedTimelineResult,
} from '../../services/unified-timeline.service';
import { isDefined, isNullOrUndefined } from '../../../../shared/utils';

interface DropdownOption {
  label: string;
  value: string;
  assetData?: AssetListItem; // Store the full asset data for device name extraction
}

interface TimeRangeOption {
  label: string;
  value: string;
  hours: number;
}

@Component({
  selector: 'app-telemetry-comparison',
  imports: [
    CommonModule,
    FormsModule,
    SelectModule,
    AutoCompleteModule,
    DatePickerModule,
    MessageModule,
    CardModule,
    ButtonModule,
    ProgressSpinnerModule,
  ],
  templateUrl: './telemetry-comparison.component.html',
  styleUrl: './telemetry-comparison.component.scss',
})
export class TelemetryComparisonComponent implements OnInit {
  private readonly clientsService = inject(ClientsService);
  private readonly assetsService = inject(AssetsService);
  private readonly authDataService = inject(AuthDataService);
  private readonly utilityService = inject(UtilityService);
  private readonly telemetryComparisonService = inject(
    TelemetryComparisonService,
  );
  private readonly eventsComparisonService = inject(EventsComparisonService);
  private readonly tripsComparisonService = inject(TripsComparisonService);
  private readonly unifiedTimelineService = inject(UnifiedTimelineService);

  title = 'Telemetry Comparison';

  // Signals for reactive state management
  clients = signal<DropdownOption[]>([]);
  assets = signal<DropdownOption[]>([]);
  filteredClients = signal<DropdownOption[]>([]);
  filteredAssets = signal<DropdownOption[]>([]);
  selectedClient = signal<DropdownOption | null>(null);
  selectedAsset = signal<DropdownOption | null>(null);
  loadingClients = signal(false);
  loadingAssets = signal(false);
  errorMessage = signal<string | null>(null);
  currentOwnerId = signal<string | null>(null);

  // Date/time range signals
  startDate = signal<Date | null>(null);
  selectedTimeRange = signal<TimeRangeOption | null>(null);
  timeRangeOptions = signal<TimeRangeOption[]>([
    { label: '20 minutes', value: '20min', hours: 0.33 },
    { label: '1 hour', value: '1h', hours: 1 },
    { label: '4 hours', value: '4h', hours: 4 },
    { label: '12 hours', value: '12h', hours: 12 },
  ]);

  // Key API credentials signals
  keyApiUsername = signal('');
  keyApiPassword = signal('');

  // Unified timeline comparison signals
  unifiedTimeline = signal<UnifiedTimelineRecord[]>([]);
  unifiedSummary = signal<UnifiedTimelineSummary | null>(null);
  isAnalyzing = signal(false);
  analysisComplete = signal(false);
  expandedRecords = signal<Set<number>>(new Set());

  // Legacy telemetry-only comparison (for fallback)
  alignedRecords = signal<AlignedRecord[]>([]);
  comparisonSummary = signal<ComparisonSummary | null>(null);

  // UI state for collapsible search area
  showCompactSummary = signal(true);
  showFullSearchForm = signal(false);

  // Record type filters
  showTelemetry = signal(true);
  showEvents = signal(true);
  showTrips = signal(true);

  // Pagination signals
  currentRecordIndex = signal(0);
  pageSize = signal(1); // Show one record at a time
  showPagination = signal(true);

  // Computed properties for template usage
  readonly selectedClientName = computed(() => {
    const client = this.selectedClient();
    return client?.label ?? 'Unknown';
  });

  readonly selectedAssetName = computed(() => {
    const asset = this.selectedAsset();
    return asset?.label ?? 'Unknown';
  });

  readonly endDate = computed(() => {
    const start = this.startDate();
    const timeRange = this.selectedTimeRange();
    if (!start || !timeRange) return null;

    const endDateTime = new Date(
      start.getTime() + timeRange.hours * 60 * 60 * 1000,
    );
    return endDateTime;
  });

  readonly isReadyForAnalysis = computed(() => {
    return !!(
      isDefined(this.selectedClient()?.value) &&
      isDefined(this.selectedAsset()?.value) &&
      isDefined(this.startDate()) &&
      isDefined(this.selectedTimeRange()) &&
      this.keyApiUsername().trim() &&
      this.keyApiPassword().trim()
    );
  });

  readonly filteredRecords = computed(() => {
    const records =
      this.unifiedTimeline().length > 0
        ? this.unifiedTimeline()
        : this.alignedRecords();

    if (this.unifiedTimeline().length > 0) {
      // Filter unified timeline records based on type toggles
      return records.filter((record) => {
        if (this.isUnifiedTimelineRecord(record)) {
          if (record.type === 'telemetry') return this.showTelemetry();
          if (record.type === 'event') return this.showEvents();
          if (record.type === 'trip_start' || record.type === 'trip_end')
            return this.showTrips();
        }
        return true; // Show unknown types by default
      });
    } else {
      // For legacy telemetry-only records, show all if telemetry filter is on
      return this.showTelemetry() ? records : [];
    }
  });

  readonly currentRecord = computed(() => {
    const records = this.filteredRecords();
    const index = this.currentRecordIndex();
    return records[index] ?? null;
  });

  readonly totalRecords = computed(() => {
    return this.filteredRecords().length;
  });

  readonly hasPrevious = computed(() => {
    return this.currentRecordIndex() > 0;
  });

  readonly hasNext = computed(() => {
    return this.currentRecordIndex() < this.totalRecords() - 1;
  });

  readonly recordPosition = computed(() => {
    const current = this.currentRecordIndex() + 1;
    const total = this.totalRecords();
    return `${current} of ${total}`;
  });

  // Filter button labels with counts
  readonly telemetryFilterLabel = computed(() => {
    const count = this.unifiedTimeline().filter(
      (r) => r.type === 'telemetry',
    ).length;
    return `üì° Telemetry (${count})`;
  });

  readonly eventsFilterLabel = computed(() => {
    const count = this.unifiedTimeline().filter(
      (r) => r.type === 'event',
    ).length;
    return `‚ö° Events (${count})`;
  });

  readonly tripsFilterLabel = computed(() => {
    const count = this.unifiedTimeline().filter(
      (r) => r.type === 'trip_start' || r.type === 'trip_end',
    ).length;
    return `üöó Trips (${count})`;
  });

  constructor() {}

  private isUnifiedTimelineRecord(
    record: unknown,
  ): record is UnifiedTimelineRecord {
    return (
      isDefined(record) &&
      typeof record === 'object' &&
      record !== null &&
      'type' in record
    );
  }

  ngOnInit(): void {
    // Initialize with default start date (1 hour ago) and time range (1 hour)
    const now = new Date();
    const oneHourAgo = new Date(now.getTime() - 60 * 60 * 1000);
    this.startDate.set(oneHourAgo);
    this.selectedTimeRange.set(this.timeRangeOptions()[1]); // 1 hour option

    // Get the current user's owner ID for listing clients
    const userProfile = this.authDataService.getUserProfile();

    if (isDefined(userProfile?.owner?.id)) {
      this.currentOwnerId.set(userProfile.owner.id);
      this.loadInitialClients(userProfile.owner.id);
    } else {
      this.errorMessage.set('Unable to determine user owner ID');
    }
  }

  private loadInitialClients(ownerId: string): void {
    this.loadingClients.set(true);
    this.errorMessage.set(null);

    // Use the same filter approach as client-switcher for consistency
    const filters: Record<
      string,
      FilterMetadata | FilterMetadata[] | string | undefined
    > = {
      state: {
        value: 'active',
        operator: '=',
      },
    };

    const rqlFilter = this.utilityService.constructRqlFilter(filters);

    this.clientsService
      .listClients(ownerId, 0, 100, 'name:asc', rqlFilter)
      .subscribe({
        next: (response: ClientListResponse) => {
          const clientOptions: DropdownOption[] =
            response.items?.map((client) => ({
              label: client.name || `Client ${client.id}`,
              value: client.id,
            })) ?? [];

          this.clients.set(clientOptions);
          this.filteredClients.set(clientOptions);
          this.loadingClients.set(false);
        },
        error: (_error) => {
          this.errorMessage.set('Failed to load clients. Please try again.');
          this.loadingClients.set(false);
        },
      });
  }

  onSearchClients(event: { query: string }): void {
    const query = event.query?.trim() || '';

    const ownerId = this.currentOwnerId();
    if (!isDefined(ownerId)) {
      return;
    }

    this.loadingClients.set(true);

    // Use the same filter approach as client-switcher
    const filters: Record<
      string,
      FilterMetadata | FilterMetadata[] | string | undefined
    > = {
      state: {
        value: 'active',
        operator: '=',
      },
    };

    if (query.length > 0) {
      filters['name'] = {
        value: `*${query}*`,
        operator: '=',
      };
    }

    const rqlFilter = this.utilityService.constructRqlFilter(filters);

    this.clientsService
      .listClients(ownerId, 0, 100, 'name:asc', rqlFilter)
      .subscribe({
        next: (response: ClientListResponse) => {
          const clientOptions: DropdownOption[] =
            response.items?.map((client) => ({
              label: client.name || `Client ${client.id}`,
              value: client.id,
            })) ?? [];

          this.filteredClients.set(clientOptions);
          this.loadingClients.set(false);
        },
        error: (_error) => {
          this.filteredClients.set([]);
          this.loadingClients.set(false);
        },
      });
  }

  onClientSelect(event: { value: DropdownOption }): void {
    const client = event.value;
    this.selectedClient.set(client);
    this.selectedAsset.set(null); // Reset asset selection
    this.assets.set([]); // Clear assets
    this.filteredAssets.set([]); // Clear filtered assets

    if (client?.value) {
      this.loadInitialAssets(client.value);
    }
  }

  onClientUnselect(): void {
    this.selectedClient.set(null);
    this.selectedAsset.set(null);
    this.assets.set([]);
    this.filteredAssets.set([]);
  }

  private loadInitialAssets(clientId: string): void {
    this.loadingAssets.set(true);
    this.errorMessage.set(null);

    // Load a large set of assets for client-side filtering
    this.assetsService.listAssets(clientId, 0, 1000).subscribe({
      next: (response: AssetListResponse) => {
        const assetOptions: DropdownOption[] =
          response.items?.map((asset) => ({
            label: asset.name || `Asset ${asset.id}`,
            value: asset.id,
            assetData: asset, // Store the full asset data
          })) ?? [];

        this.assets.set(assetOptions);
        this.filteredAssets.set(assetOptions);
        this.loadingAssets.set(false);
      },
      error: () => {
        this.errorMessage.set('Failed to load assets. Please try again.');
        this.loadingAssets.set(false);
      },
    });
  }

  onSearchAssets(event: { query: string }): void {
    const query = event.query.trim().toLowerCase();

    const allAssets = this.assets();

    if (!query) {
      this.filteredAssets.set(allAssets);
      return;
    }

    // Client-side filtering
    const filtered = allAssets.filter((asset) =>
      asset.label.toLowerCase().includes(query),
    );

    this.filteredAssets.set(filtered);
  }

  onAssetSelect(event: { value: DropdownOption }): void {
    const asset = event.value;
    this.selectedAsset.set(asset);
    // Here you can add logic to load telemetry data for the selected asset
    if (asset?.value && isDefined(this.selectedClient()?.value)) {
      // TODO: Load telemetry data
    }
  }

  onAssetUnselect(): void {
    this.selectedAsset.set(null);
  }

  onStartDateChange(date: Date | null): void {
    this.startDate.set(date);
  }

  onTimeRangeChange(timeRange: TimeRangeOption | null): void {
    this.selectedTimeRange.set(timeRange);

    // Auto-adjust start date if needed when duration changes
    if (timeRange && this.startDate()) {
      const currentStartDate = this.startDate();
      if (currentStartDate) {
        const now = new Date();
        const durationMs = timeRange.hours * 60 * 60 * 1000; // Convert hours to milliseconds
        const endDate = new Date(currentStartDate.getTime() + durationMs);

        // If the calculated end date would be in the future (beyond current time),
        // adjust the start date backwards to keep the end date at or before now
        if (endDate > now) {
          const adjustedStartDate = new Date(now.getTime() - durationMs);
          this.startDate.set(adjustedStartDate);
        }
      }
    }
  }

  /**
   * Extract device name from the selected asset
   */
  private getDeviceNameFromAsset(asset: DropdownOption): string | undefined {
    const assetData = asset.assetData;
    if (
      !assetData?.devices ||
      !Array.isArray(assetData.devices) ||
      assetData.devices.length === 0
    ) {
      return undefined;
    }

    // Use the first device's name
    const firstDevice = assetData.devices[0];
    const deviceName = firstDevice?.name ?? undefined;

    return deviceName;
  }

  onAnalyzeTelemetry(): void {
    const _client = this.selectedClient();
    const asset = this.selectedAsset();
    const startDate = this.startDate();
    const endDate = this.endDate();
    const timeRange = this.selectedTimeRange();

    if (
      this.isReadyForAnalysis() &&
      startDate &&
      endDate &&
      isDefined(asset?.value)
    ) {
      this.isAnalyzing.set(true);
      this.errorMessage.set(null);
      this.analysisComplete.set(false);

      // Clear all previous results
      this.unifiedTimeline.set([]);
      this.unifiedSummary.set(null);
      this.alignedRecords.set([]);
      this.comparisonSummary.set(null);

      // Set Key API credentials in all comparison services
      const keyUsername = this.keyApiUsername().trim();
      const keyPassword = this.keyApiPassword().trim();

      this.telemetryComparisonService.setKeyApiCredentials(
        keyUsername,
        keyPassword,
      );
      this.eventsComparisonService.setKeyApiCredentials(
        keyUsername,
        keyPassword,
      );
      this.tripsComparisonService.setKeyApiCredentials(
        keyUsername,
        keyPassword,
      );

      // Extract device name from the selected asset
      const deviceName = this.getDeviceNameFromAsset(asset);

      // Convert local time to proper UTC for API calls
      // If user selects 16:28 local (GMT+2), we need to send 14:28 UTC to the API
      // This means subtracting the timezone offset to get the equivalent UTC time

      // toISOString() automatically converts to UTC, which is what we want

      // Use unified timeline service to get all data types
      this.unifiedTimelineService
        .getUnifiedTimeline(
          asset.value,
          startDate,
          endDate,
          200, // Limit to 200 records for now
          deviceName, // Pass device name for vehicle mapping
        )
        .subscribe({
          next: (result: UnifiedTimelineResult) => {
            // Check if CypherView has any data for this vehicle/time period
            const _cypherViewRecords = result.records.filter(
              (r) => r.testRecord,
            );
            const totalCypherViewData =
              result.rawData.telemetry.testRecords.length +
              result.rawData.events.testRecords.length +
              result.rawData.trips.testRecords.length;

            if (totalCypherViewData === 0) {
              this.isAnalyzing.set(false);
              this.analysisComplete.set(false);
              this.errorMessage.set(
                `No CypherView data found for ${asset?.label} during the selected time period (${timeRange?.label}). ` +
                  `Please verify that the device was pointing to CypherView ingestion services during this time.`,
              );
              return;
            }

            this.unifiedTimeline.set(result.records);
            this.unifiedSummary.set(result.summary);
            this.isAnalyzing.set(false);
            this.analysisComplete.set(true);
            this.currentRecordIndex.set(0);

            // Auto-expand all records for easy viewing
            this.expandAllRecords();
          },
          error: (_error) => {
            this.errorMessage.set(
              'Failed to analyze unified timeline. Please try again.',
            );
            this.isAnalyzing.set(false);
            this.analysisComplete.set(false);
          },
        });
    } else {
      this.errorMessage.set(
        'Please complete all fields including Key API credentials.',
      );
    }
  }

  toggleRecordExpansion(index: number): void {
    const expanded = this.expandedRecords();
    const newExpanded = new Set(expanded);

    if (newExpanded.has(index)) {
      newExpanded.delete(index);
    } else {
      newExpanded.add(index);
    }

    this.expandedRecords.set(newExpanded);
  }

  getFieldComparisons(
    record: UnifiedTimelineRecord | AlignedRecord,
  ): FieldComparison[] {
    // Handle both unified timeline records and legacy aligned records
    if (this.isUnifiedTimelineRecord(record)) {
      // This is a UnifiedTimelineRecord
      switch (record.type) {
        case 'telemetry':
          return this.telemetryComparisonService.compareFields(
            record.testRecord,
            record.prodRecord,
          );
        case 'event':
          return this.eventsComparisonService.compareFields(
            record.testRecord,
            record.prodRecord,
          );
        case 'trip_start':
        case 'trip_end':
          return this.tripsComparisonService.compareFields(
            record.testRecord,
            record.prodRecord,
          );
        default:
          return [];
      }
    } else {
      // This is a legacy AlignedRecord
      const alignedRecord = record;
      return this.telemetryComparisonService.compareFields(
        alignedRecord.testRecord,
        alignedRecord.prodRecord,
      );
    }
  }

  getRecordTimestamp(record: UnifiedTimelineRecord | AlignedRecord): string {
    // Handle both unified timeline records and legacy aligned records
    if (this.isUnifiedTimelineRecord(record)) {
      // This is a UnifiedTimelineRecord
      const timestamp = record.timestamp;

      try {
        // Parse the UTC timestamp from server
        const dt = new Date(timestamp);
        // Display in browser's local timezone
        return dt.toLocaleString('en-GB', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
          // Remove timeZone to use browser's local timezone
        });
      } catch {
        return timestamp;
      }
    } else {
      // This is a legacy AlignedRecord
      const alignedRecord = record;
      const dataRecord = alignedRecord.testRecord || alignedRecord.prodRecord;
      if (!dataRecord) return 'UNKNOWN TIME';

      const timestamp =
        dataRecord.timestamp ?? dataRecord['date'] ?? dataRecord.received;
      if (isNullOrUndefined(timestamp)) return 'UNKNOWN TIME';

      try {
        // Parse the UTC timestamp from server
        const dt = new Date(timestamp as string);
        // Display in browser's local timezone
        return dt.toLocaleString('en-GB', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
          // Remove timeZone to use browser's local timezone
        });
      } catch {
        return timestamp as string;
      }
    }
  }

  getRecordStatus(record: UnifiedTimelineRecord | AlignedRecord): string {
    // Handle both unified timeline records and legacy aligned records
    if (this.isUnifiedTimelineRecord(record)) {
      // This is a UnifiedTimelineRecord
      return record.status;
    } else {
      // This is a legacy AlignedRecord
      const alignedRecord = record;
      if (alignedRecord.testRecord && alignedRecord.prodRecord) {
        return 'both-environments';
      } else if (alignedRecord.testRecord) {
        return 'cypherView-only';
      } else {
        return 'key-only';
      }
    }
  }

  getRecordStatusIcon(record: UnifiedTimelineRecord | AlignedRecord): string {
    // Handle both unified timeline records and legacy aligned records
    if (this.isUnifiedTimelineRecord(record)) {
      // This is a UnifiedTimelineRecord - just show status
      if (record.testRecord && record.prodRecord) {
        return '‚úÖ';
      } else if (record.testRecord) {
        return 'üîπ';
      } else {
        return 'üî∏';
      }
    } else {
      // This is a legacy AlignedRecord
      const alignedRecord = record;
      if (alignedRecord.testRecord && alignedRecord.prodRecord) {
        return '‚úÖ';
      } else if (alignedRecord.testRecord) {
        return 'üîπ';
      } else {
        return 'üî∏';
      }
    }
  }

  getRecordTypeDisplay(record: UnifiedTimelineRecord | AlignedRecord): string {
    // Handle both unified timeline records and legacy aligned records
    if (this.isUnifiedTimelineRecord(record)) {
      // This is a UnifiedTimelineRecord
      switch (record.type) {
        case 'telemetry':
          return 'TELEMETRY';
        case 'event':
          return 'EVENT';
        case 'trip_start':
          return 'TRIP START';
        case 'trip_end':
          return 'TRIP END';
        default:
          return 'UNKNOWN';
      }
    } else {
      // This is a legacy AlignedRecord - assume telemetry
      return 'TELEMETRY';
    }
  }

  getRecordTypeBadgeClass(
    record: UnifiedTimelineRecord | AlignedRecord,
  ): string {
    // Handle both unified timeline records and legacy aligned records
    if (this.isUnifiedTimelineRecord(record)) {
      // This is a UnifiedTimelineRecord
      switch (record.type) {
        case 'telemetry':
          return 'badge-telemetry';
        case 'event':
          return 'badge-event';
        case 'trip_start':
        case 'trip_end':
          return 'badge-trip';
        default:
          return 'badge-unknown';
      }
    } else {
      // This is a legacy AlignedRecord - assume telemetry
      return 'badge-telemetry';
    }
  }
  clearAnalysis(): void {
    this.alignedRecords.set([]);
    this.comparisonSummary.set(null);
    this.analysisComplete.set(false);
    this.expandedRecords.set(new Set());
    this.currentRecordIndex.set(0);
    // Keep credentials for convenience
  }

  // Pagination methods
  goToPreviousRecord(): void {
    if (this.hasPrevious()) {
      this.currentRecordIndex.update((index) => index - 1);
    }
  }

  goToNextRecord(): void {
    if (this.hasNext()) {
      this.currentRecordIndex.update((index) => index + 1);
    }
  }

  goToRecord(index: number): void {
    if (index >= 0 && index < this.totalRecords()) {
      this.currentRecordIndex.set(index);
    }
  }

  expandAllRecords(): void {
    const totalRecords = this.totalRecords();
    const allIndices = new Set(
      Array.from({ length: totalRecords }, (_, i) => i),
    );
    this.expandedRecords.set(allIndices);
  }

  collapseAllRecords(): void {
    this.expandedRecords.set(new Set());
  }

  toggleSearchArea(): void {
    this.showCompactSummary.update((current) => !current);
    this.showFullSearchForm.update((current) => !current);
  }

  restartAnalysis(): void {
    // Clear all results and show full search form
    this.analysisComplete.set(false);
    this.unifiedTimeline.set([]);
    this.unifiedSummary.set(null);
    this.alignedRecords.set([]);
    this.comparisonSummary.set(null);
    this.errorMessage.set(null);
    this.currentRecordIndex.set(0);
    this.expandedRecords.set(new Set());

    // Reset filters
    this.showTelemetry.set(true);
    this.showEvents.set(true);
    this.showTrips.set(true);

    // Show search form
    this.showCompactSummary.set(false);
    this.showFullSearchForm.set(false);
  }

  toggleTelemetryFilter(): void {
    this.showTelemetry.update((current) => !current);
    this.resetToFirstRecord();
  }

  toggleEventsFilter(): void {
    this.showEvents.update((current) => !current);
    this.resetToFirstRecord();
  }

  toggleTripsFilter(): void {
    this.showTrips.update((current) => !current);
    this.resetToFirstRecord();
  }

  private resetToFirstRecord(): void {
    // Reset to first record when filters change
    this.currentRecordIndex.set(0);
  }

  // Keyboard navigation
  onKeyDown(event: KeyboardEvent): void {
    if (event.key === 'ArrowLeft' || event.key === 'ArrowUp') {
      event.preventDefault();
      this.goToPreviousRecord();
    } else if (event.key === 'ArrowRight' || event.key === 'ArrowDown') {
      event.preventDefault();
      this.goToNextRecord();
    } else if (event.key === 'Home') {
      event.preventDefault();
      this.goToRecord(0);
    } else if (event.key === 'End') {
      event.preventDefault();
      this.goToRecord(this.totalRecords() - 1);
    }
  }
}
